import { connect } from 'react-redux';
import { Link, Route } from 'react-router-dom';
import Immutable from 'immutable';
import React from 'react';

import styles from './StoryList.scss';


class StoryList extends React.Component {

  static STORY_LIST = Immutable.List([
    { name: "test", id: 'test'},
    { name: " - part 1", id: 'story_sisi'},
    { name: " - part 2", id: 'story_sisi_2'},
    { name: "", id: 'selfie'},
    { name: "", id: 'abdication'},
    { name: "-old_version", id: 'zhengzaishuru0504'},
    { name: "", id: 'zhengzaishuru0517'},
    { name: "(kd)", id: 'huijialushang'},
  ]);

  render() {
    let { match } = this.props;
    let prefix = location.origin;
    if (process.env.NODE_ENV === 'gh') {
      prefix = prefix + '/#';
    } 

    let matchPath = match.url;
    if (!matchPath.endsWith('/')) {
      matchPath = matchPath + '/';
    }

    return (
      <div className={styles.storyListContainer}>
        <p>Celine, list</p>
        <ul>
          { 
            StoryList.STORY_LIST.map((story, index) => {
              return (
                <li key={index}>
                  <Link to={`${matchPath}${story.id}`}> {story.name} </Link>
                  : {`${prefix}${matchPath}${story.id}`}
                </li>
              );
            })
          }
        </ul>

        <svg className={styles.hearts} dangerouslySetInnerHTML={{__html: require('../assets/images/hearts.svg')}} />
      </div>
    );
  }
}

export default connect(null, {})(StoryList);



